'use client';

import { useEffect, useRef, useState } from 'react';

export default function SkinCard({ skin, onEdit, onDelete, onCopyLink }) {
  const canvasRef = useRef(null);
  const [skinViewer, setSkinViewer] = useState(null);
  const [showDeleteModal, setShowDeleteModal] = useState(false);

  useEffect(() => {
    if (canvasRef.current && typeof window !== 'undefined') {
      initSkinViewer();
    }

    return () => {
      if (skinViewer) {
        skinViewer.dispose();
      }
    };
  }, []);

  const initSkinViewer = async () => {
    try {
      // Importer skinview3d dynamiquement
      const { SkinViewer } = await import('skinview3d');
      
      const viewer = new SkinViewer({
        canvas: canvasRef.current,
        width: 200,
        height: 200,
        skin: `${window.location.origin}${skin.path}`
      });

      // Configuration du viewer
      viewer.controls.enableRotate = true;
      viewer.controls.enableZoom = false;
      viewer.controls.enablePan = false;
      
      // Animation de rotation automatique
      viewer.autoRotate = true;
      viewer.autoRotateSpeed = 1;

      setSkinViewer(viewer);
    } catch (error) {
      console.error('Erreur lors du chargement du viewer 3D:', error);
    }
  };

  const handleCopyLink = () => {
    const link = `/skin "${window.location.origin}${skin.path}"`;
    navigator.clipboard.writeText(link).then(() => {
      onCopyLink && onCopyLink(link);
    });
  };

  const handleDelete = () => {
    setShowDeleteModal(false);
    onDelete(skin.id);
  };

  return (
    <>
      <div className="bg-white rounded-xl shadow-lg overflow-hidden hover:shadow-xl transition-shadow duration-300">
        <div className="p-4">
          <div className="flex justify-center mb-4">
            <canvas
              ref={canvasRef}
              className="border border-gray-200 rounded-lg"
              style={{ maxWidth: '100%', height: 'auto' }}
            />
          </div>
          
          <h3 className="text-lg font-semibold text-gray-900 mb-2 text-center truncate">
            {skin.name}
          </h3>
          
          <p className="text-sm text-gray-500 text-center mb-4">
            Créé le {new Date(skin.createdAt).toLocaleDateString('fr-FR')}
          </p>
          
          <div className="flex flex-col gap-2">
            <button
              onClick={() => onEdit(skin)}
              className="w-full bg-blue-600 hover:bg-blue-700 text-white font-medium py-2 px-4 rounded-lg transition duration-200"
            >
              Modifier
            </button>
            
            <button
              onClick={handleCopyLink}
              className="w-full bg-green-600 hover:bg-green-700 text-white font-medium py-2 px-4 rounded-lg transition duration-200"
            >
              Copier le lien
            </button>
            
            <button
              onClick={() => setShowDeleteModal(true)}
              className="w-full bg-red-600 hover:bg-red-700 text-white font-medium py-2 px-4 rounded-lg transition duration-200"
            >
              Supprimer
            </button>
          </div>
        </div>
      </div>

      {/* Modal de confirmation de suppression */}
      {showDeleteModal && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-xl p-6 max-w-md w-full">
            <h3 className="text-lg font-semibold text-gray-900 mb-4">
              Confirmer la suppression
            </h3>
            <p className="text-gray-600 mb-6">
              Êtes-vous sûr de vouloir supprimer le skin "{skin.name}" ? Cette action est irréversible.
            </p>
            <div className="flex gap-3">
              <button
                onClick={() => setShowDeleteModal(false)}
                className="flex-1 bg-gray-200 hover:bg-gray-300 text-gray-800 font-medium py-2 px-4 rounded-lg transition duration-200"
              >
                Annuler
              </button>
              <button
                onClick={handleDelete}
                className="flex-1 bg-red-600 hover:bg-red-700 text-white font-medium py-2 px-4 rounded-lg transition duration-200"
              >
                Supprimer
              </button>
            </div>
          </div>
        </div>
      )}
    </>
  );
}
