'use client';

import { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import SkinCard from '@/components/SkinCard';
import UploadForm from '@/components/UploadForm';
import EditForm from '@/components/EditForm';

export default function DashboardPage() {
  const [user, setUser] = useState(null);
  const [skins, setSkins] = useState([]);
  const [loading, setLoading] = useState(true);
  const [showUploadForm, setShowUploadForm] = useState(false);
  const [editingSkin, setEditingSkin] = useState(null);
  const [message, setMessage] = useState('');
  const router = useRouter();

  useEffect(() => {
    checkAuth();
  }, []);

  const checkAuth = async () => {
    try {
      const response = await fetch('/api/auth/me');
      if (response.ok) {
        const userData = await response.json();
        setUser(userData.user);
        loadSkins();
      } else {
        router.push('/login');
      }
    } catch (error) {
      router.push('/login');
    } finally {
      setLoading(false);
    }
  };

  const loadSkins = async () => {
    try {
      const response = await fetch('/api/skins/list');
      if (response.ok) {
        const data = await response.json();
        setSkins(data.skins);
      }
    } catch (error) {
      console.error('Erreur lors du chargement des skins:', error);
    }
  };

  const handleLogout = async () => {
    try {
      await fetch('/api/auth/logout', { method: 'POST' });
      router.push('/');
    } catch (error) {
      console.error('Erreur lors de la déconnexion:', error);
    }
  };

  const handleUpload = (newSkin) => {
    setSkins([newSkin, ...skins]);
    setMessage('Skin uploadé avec succès !');
    setTimeout(() => setMessage(''), 3000);
  };

  const handleEdit = (updatedSkin) => {
    setSkins(skins.map(skin => 
      skin.id === updatedSkin.id 
        ? { ...skin, name: updatedSkin.name, path: updatedSkin.path }
        : skin
    ));
    setMessage('Skin modifié avec succès !');
    setTimeout(() => setMessage(''), 3000);
  };

  const handleDelete = async (skinId) => {
    try {
      const response = await fetch(`/api/skins/delete?id=${skinId}`, {
        method: 'DELETE'
      });

      if (response.ok) {
        setSkins(skins.filter(skin => skin.id !== skinId));
        setMessage('Skin supprimé avec succès !');
        setTimeout(() => setMessage(''), 3000);
      } else {
        const data = await response.json();
        setMessage(`Erreur: ${data.error}`);
        setTimeout(() => setMessage(''), 5000);
      }
    } catch (error) {
      setMessage('Erreur lors de la suppression');
      setTimeout(() => setMessage(''), 5000);
    }
  };

  const handleCopyLink = (link) => {
    setMessage('Lien copié dans le presse-papiers !');
    setTimeout(() => setMessage(''), 3000);
  };

  if (loading) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto"></div>
          <p className="mt-4 text-gray-600">Chargement...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <header className="bg-white shadow-sm border-b">
        <div className="container mx-auto px-4 py-4">
          <div className="flex justify-between items-center">
            <div>
              <h1 className="text-2xl font-bold text-gray-900">Dashboard</h1>
              <p className="text-gray-600">Bienvenue, {user?.username}</p>
            </div>
            <div className="flex items-center gap-4">
              <span className="text-sm text-gray-500">
                {skins.length}/10 skins
              </span>
              <button
                onClick={handleLogout}
                className="bg-red-600 hover:bg-red-700 text-white font-medium py-2 px-4 rounded-lg transition duration-200"
              >
                Déconnexion
              </button>
            </div>
          </div>
        </div>
      </header>

      {/* Message de notification */}
      {message && (
        <div className="container mx-auto px-4 pt-4">
          <div className="bg-green-50 border border-green-200 text-green-700 px-4 py-3 rounded-lg">
            {message}
          </div>
        </div>
      )}

      {/* Contenu principal */}
      <main className="container mx-auto px-4 py-8">
        {/* Bouton d'upload */}
        <div className="mb-8">
          <button
            onClick={() => setShowUploadForm(true)}
            disabled={skins.length >= 10}
            className="bg-blue-600 hover:bg-blue-700 text-white font-semibold py-3 px-6 rounded-lg transition duration-200 disabled:opacity-50 disabled:cursor-not-allowed flex items-center gap-2"
          >
            <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4v16m8-8H4" />
            </svg>
            {skins.length >= 10 ? 'Limite atteinte (10/10)' : 'Uploader un nouveau skin'}
          </button>
        </div>

        {/* Liste des skins */}
        {skins.length === 0 ? (
          <div className="text-center py-12">
            <div className="text-gray-400 mb-4">
              <svg className="w-16 h-16 mx-auto" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1} d="M19 11H5m14 0a2 2 0 012 2v6a2 2 0 01-2 2H5a2 2 0 01-2-2v-6a2 2 0 012-2m14 0V9a2 2 0 00-2-2M5 11V9a2 2 0 012-2m0 0V5a2 2 0 012-2h6a2 2 0 012 2v2M7 7h10" />
              </svg>
            </div>
            <h3 className="text-lg font-medium text-gray-900 mb-2">Aucun skin</h3>
            <p className="text-gray-600 mb-4">Commencez par uploader votre premier skin !</p>
            <button
              onClick={() => setShowUploadForm(true)}
              className="bg-blue-600 hover:bg-blue-700 text-white font-medium py-2 px-4 rounded-lg transition duration-200"
            >
              Uploader un skin
            </button>
          </div>
        ) : (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6">
            {skins.map((skin) => (
              <SkinCard
                key={skin.id}
                skin={skin}
                onEdit={setEditingSkin}
                onDelete={handleDelete}
                onCopyLink={handleCopyLink}
              />
            ))}
          </div>
        )}
      </main>

      {/* Modals */}
      {showUploadForm && (
        <UploadForm
          onUpload={handleUpload}
          onClose={() => setShowUploadForm(false)}
        />
      )}

      {editingSkin && (
        <EditForm
          skin={editingSkin}
          onEdit={handleEdit}
          onClose={() => setEditingSkin(null)}
        />
      )}
    </div>
  );
}
